import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Promo from '../views/Promo.vue'
import Search_result from '../views/Search_result.vue'
import Search_result2 from '../views/Search_result2.vue'
import Search_result3 from '../views/Search_result3.vue'
import Bestrate from '../views/Bestrate.vue'
import Allin from '../views/Allin.vue'
import Payment from '../views/payment.vue'
import Event from '../views/Event.vue'
import Addition from '../views/Addition.vue'
import Cart from '../views/Cart.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/promo',
    name: 'Promo',
    component: Promo
  },
  {
    path: '/allin',
    name: 'Allin',
    component: Allin
  },
  {
    path: '/bestrate',
    name: 'Bestrate',
    component: Bestrate
  },
  {
    path: '/search_result',
    name: 'Search_result',
    component: Search_result
  },
  {
    path: '/search_result2',
    name: 'Search_result2',
    component: Search_result2
  },
  {
    path: '/search_result3',
    name: 'Search_result3',
    component: Search_result3
  },
  {
    path: '/event',
    name: 'Event',
    component: Event
  },
  {
    path: '/addition',
    name: 'Addition',
    component: Addition
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  {
    path: '/payment',
    name: 'Payment',
    component: Payment
  },

  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/About.vue')
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
